.card-product {
  width: 100%;
  border: 1px solid #f4f4f4;
  background-color: #ffffff;
  box-sizing: border-box;
  color: #999999;
  font-size: 12px;
  text-align: center;
  border-radius: 8px;
  margin: 5px;
}

.card-image {
  width: 270px;
  height: 210px;
  box-sizing: border-box;
  padding: 10px 10px;
  margin: 0 auto;
}
img {
  height: 180px;
  margin: 0 auto;
}

.card-info {
  width: 100%;
  border-top: 1px solid #f4f4f4;
  box-sizing: border-box;
  padding: 10px 10px;
  margin: 0 auto;
  min-height: 90px;
}
